@charset "UTF-8";
html { -webkit-tap-highlight-color: transparent;  }
body { width: 100%; font-family: "Arial","微软雅黑"; font-size: 12px;  }
input,button,select,textarea { outline:none; }
body, p, h1, h2, h3, ul, dl, dt, dd, input, select ,textarea { margin: 0; padding: 0; }
li { list-style: none; }
img { border: none; vertical-align: top; }
a:link, a:visited { text-decoration: none; }
a:hover, a:focus, a:active { text-decoration: none; }
.hide {	display: none; }
.clearfix { zoom: 1; }
.clearfix:after { display: block; content: ''; clear: both; }

#layout{max-width: 750px;width: 100%;margin: 0 auto;}
header { position: relative; height: 40px; padding: 8px 0 8px 24px; background: #e9546b;overflow: hidden; }
header img { width: 147px; height: 40px; }
header a { position: absolute; top: 10px; right: 24px;text-align: center;}
header a span { display: block;font-size: 20px; line-height: 35px; color: #fff; }

.content{ width: 100%; }
.content img { width: 100%; }

footer { padding: 6px 0 2px; text-align: center; background: #fff; }
footer img { width: 140px;height: 140px; }
footer p { font-weight: normal;font-size: 20px; }
footer p.phone{ line-height: 18px; font-size: 18px; }
